.logo {
  position: relative;
  width: 3rem;
  height: 3rem;
}

.logo img {
  width: 100%;
}

.logo::after {
  content: "";
  position: absolute;
  background: #fff;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  border-radius: 50%;
  transform: scale(0.8);
}
